.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
h1 {
  margin-bottom: 0;
  margin-top: 4rem;
}
.alert.info {
  background-color: transparent;
  padding: 1rem 3rem;
  color: rgb(131, 130, 130);
  border-radius: .5rem;
  font-size: 1rem;
  width: max-content;
  text-align: center;
}
.wrapper-inputs {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
}
input {
  display: flex;
  width:  3.5rem;
  height: 3.5rem;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  margin: .5rem;
  border: 2px solid rgb(79, 131, 162);
  cursor: pointer;
  transition: 0.3s;
  -moz-appearance: textfield;
}
input[type="number"] {
  border-radius: 1rem;
  width: 30%;
  text-align: center;
  background-color: rgb(46, 48, 49);
  color: white;
}
input.submit {
  border-radius: 1rem;
  width: max-content;
  padding: .5rem 1rem;
  height: 5rem;
  width: 90%;
  margin-top: 1rem;
  text-align: center;
  background-color: rgb(46, 48, 49);
  border-color: white;
  color: white;
  transition: 0.2s;
}
input.submit:hover {
  background-color: rgb(79, 131, 162);
}
input[type="text"] {
  position: absolute;
  left: -200vw;
}
.active {
  background-color: rgb(79, 131, 162);
  border-color: white;
  color: white;
}